'use client'

import { useEffect, useRef } from 'react'

export default function Hero() {
  const heroRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      if (!heroRef.current) return
      
      const { clientX, clientY } = e
      const { innerWidth, innerHeight } = window
      
      const xPos = (clientX / innerWidth - 0.5) * 20
      const yPos = (clientY / innerHeight - 0.5) * 20
      
      const cards = heroRef.current.querySelectorAll('.floating-card')
      cards.forEach((card, index) => {
        const element = card as HTMLElement
        const multiplier = (index + 1) * 0.5
        element.style.transform = `translate3d(${xPos * multiplier}px, ${yPos * multiplier}px, 0) rotateX(${yPos * 0.5}deg) rotateY(${xPos * 0.5}deg)`
      })
    }

    window.addEventListener('mousemove', handleMouseMove)
    return () => window.removeEventListener('mousemove', handleMouseMove)
  }, [])

  return (
    <section ref={heroRef} className="relative min-h-screen bg-gray-100 overflow-hidden flex items-center justify-center">
      {/* Background gradient */}
      <div className="absolute inset-0 bg-gradient-to-br from-gray-50 to-gray-200"></div>
      
      {/* Floating Cards */}
      <div className="relative z-10 w-full max-w-6xl mx-auto px-6">
        
        {/* Main Card - Free PSD */}
        <div className="floating-card absolute top-20 left-10 md:left-20 transform -rotate-12 transition-transform duration-300 ease-out">
          <div className="bg-red-500 text-white p-8 rounded-lg shadow-2xl w-64 h-80">
            <div className="text-sm opacity-80 mb-4">Falling In</div>
            <div className="text-4xl font-bold mb-2">free</div>
            <div className="text-lg">PSD</div>
            <div className="text-sm mt-4 opacity-80">Stationery Free</div>
            <div className="absolute bottom-4 right-4 text-xs opacity-60">
              Mockup<br/>by Freepik
            </div>
          </div>
        </div>

        {/* FIVE Card */}
        <div className="floating-card absolute top-32 right-10 md:right-32 transform rotate-6 transition-transform duration-300 ease-out">
          <div className="bg-black text-red-500 p-8 rounded-lg shadow-2xl w-72 h-96">
            <div className="text-sm text-white opacity-80 mb-4">Falling In</div>
            <div className="text-6xl font-bold">FIVE</div>
            <div className="text-white text-sm mt-4 opacity-80">Stationery Free</div>
          </div>
        </div>

        {/* Invitation Card */}
        <div className="floating-card absolute bottom-20 left-1/2 transform -translate-x-1/2 rotate-3 transition-transform duration-300 ease-out">
          <div className="bg-gray-200 text-red-500 p-8 rounded-lg shadow-2xl w-80 h-96">
            <div className="text-4xl font-bold mb-2">invt</div>
            <div className="text-sm text-gray-600 mb-4">Falling Invitation Statio-</div>
            <div className="text-6xl font-bold">card</div>
            <div className="text-sm text-gray-600 mt-4">nery Free PSD Brand</div>
            <div className="absolute bottom-4 right-4 text-xs text-gray-500">
              Mockup<br/>by Freepik
            </div>
          </div>
        </div>

        {/* Center Content */}
        <div className="text-center z-20 relative">
          <div className="mb-8">
            <div className="text-8xl md:text-9xl font-black text-black mb-4">
              Â©KAJO
            </div>
            <div className="text-6xl md:text-7xl font-black text-black">
              STUDIO
            </div>
          </div>
        </div>
      </div>

      {/* Dark mode toggle */}
      <div className="absolute bottom-8 left-8 z-20">
        <button className="bg-black text-white p-3 rounded-full hover:bg-gray-800 transition-colors">
          <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 24 24">
            <path d="M12 3v1m0 16v1m9-9h-1M4 12H3m15.364 6.364l-.707-.707M6.343 6.343l-.707-.707m12.728 0l-.707.707M6.343 17.657l-.707.707M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
          </svg>
        </button>
      </div>

      {/* Framer Branding */}
      <div className="absolute bottom-8 right-8 z-20">
        <div className="flex flex-col items-end space-y-2">
          <button className="bg-black text-white px-4 py-2 rounded-full text-sm font-medium hover:bg-gray-800 transition-colors">
            Use for FREE
          </button>
          <button className="bg-white text-black px-4 py-2 rounded-full text-sm font-medium border hover:bg-gray-50 transition-colors">
            More Templates
          </button>
          <div className="flex items-center space-x-2 text-sm text-gray-600">
            <span>ðŸŽ¨</span>
            <span>Made in Framer</span>
          </div>
        </div>
      </div>
    </section>
  )
}
