'use client'

import { useState, useEffect } from 'react'

export default function Header() {
  const [isScrolled, setIsScrolled] = useState(false)

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <header className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
      isScrolled ? 'bg-white/90 backdrop-blur-md shadow-sm' : 'bg-transparent'
    }`}>
      <div className="max-w-7xl mx-auto px-6 py-6">
        <nav className="flex items-center justify-between">
          {/* Logo */}
          <div className="text-2xl font-bold text-black">
            KAJO
          </div>

          {/* Navigation Links */}
          <div className="hidden md:flex items-center space-x-8">
            <a href="#work" className="text-black hover:text-gray-600 transition-colors font-medium">
              Work
            </a>
            <a href="#service" className="text-black hover:text-gray-600 transition-colors font-medium">
              Service
            </a>
            <a href="#about" className="text-black hover:text-gray-600 transition-colors font-medium">
              About
            </a>
            <a href="#blog" className="text-black hover:text-gray-600 transition-colors font-medium">
              Blog
            </a>
          </div>

          {/* CTA Button */}
          <div className="flex items-center">
            <button className="bg-black text-white px-6 py-3 rounded-full hover:bg-gray-800 transition-colors font-medium">
              Let's Talk
            </button>
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden">
            <button className="text-black">
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              </svg>
            </button>
          </div>
        </nav>
      </div>
    </header>
  )
}
